
import CommonHeader from '@/pages/common/header.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { GOLF_ORDER_CONFIRM } from '@/api/golfOrderConfirm'
export default {
name: 'golfComfirm',
props: {
date: {
type: String
},
position: {
type: Array
},
concat: {
type: String
},
caddie: {
type: Array
}
},
data () {
return {
invoice: false,
rise: '',
detail: false,
showToast: false,
msg: ''
}
},
computed: {
number () {
return this.position.length
},
userName () {
return JSON.parse(localStorage.getItem('userInfo')).userName
},
guestName () {
return new Array(this.number - 1)
},
totalPrice () {
return this.guestName.length * 1700 + 400
}
},
components: {
CommonHeader,
XInput,
Group,
CheckIcon,
Toast
},
methods: {
handleDetailShow () {
this.detail = !this.detail
},
handlePay () {
if (this.invoice && this.rise === '') {
this.msg = '请输入发票抬头!'
this.showToast = true
} else {
GOLF_ORDER_CONFIRM({
date: this.date,
position: this.position,
caddies: this.caddies,
concat: this.concat,
guestName: this.guestName,
invoice: this.invoice,
rise: this.rise,
totalPrice: this.totalPrice
}).then(res => {
console.log(res)
if (res.code === 200) {
this.$emit('hideConfirm')
this.$router.push('/pay/' + res.orderId)
}
})
}
},
handleHide () {
this.$emit('hideConfirm')
}
}
}



